    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                mainHeight: 716,
                isResize: true,
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                returnStatusName: {
                    '0':'',
                    '50':'',
                    '100':'',
                    'null':''
                },
                isShowChildMatCode: true,
                currentIndex: 0,
                clickTimes: 0,
                editIndex: 0,
                tableHeightI: this.$store.state.screenHei*0.62,
                tableHeightII: 150,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    //isMulSelect: true,
                    isInlineForm: true,
                    isEdit: false,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    loading: false,
                    $isCustom: true
                },
                showTable: false,
                tempWidth: null,
                tableData: [],
                showPrice: false,
                showUnPrice: false,
                //
                searchProp: [
                    {
                        name:'',
                        prop:'refDocCode',
                        type:'date',
                        isMainSearch:true,
                        isMain:false,
                        placeholder: ''
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:0,label:''},{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'refDocCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'deliveryDate',type:'outter'},
                    {name:'',prop:'priceStatus',type:'outter',isStatus:true},
                    {name:'',prop:'isCheckPrice',type:'outter',isCheck:true},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'contractCode',type:'inner'},
                    {name:'',prop:'alPriceMethodName',type:'inner'},
                    {name:'',prop:'alPrice',type:'inner'},
                    {name:'',prop:'companyName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'settlementMethodName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'refDocCode',
                        name2:'',
                        prop2:'priceStatus',
                        type:'is_check_price',
                        width: '130'
                    },
                    {
                        name1:'/',
                        prop1:'docCode',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '130'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'destination',
                        name2:'',
                        prop2:'loaderMethodName',
                        type:'mixin',
                    }
                ],
                searchForm: {
                    minPriceStatus: 0,
                    maxPriceStatus: 50
                },
                detailForm: {},
                detailId: null,
                contractCode: null,
                docCode: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                unitList: [],
                //
                tabLeft: '100px',
                activeName: 'first',
                //
                tabDataI: [],
                copyDataI: [],
                loadingI: false,
                isShowNotAllowed: false,
                refMatPrice: null,
                refMatProcessPrice: null,
                tabPropIII: [
                    {prop:'itemNo', name:'',},
                    {prop:'priceItemText', name:'',type:'normal',width:'100'},
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'otherPrice',name:'(/m)',type:'normal'},
                            {prop:'price',name:'(/kg)',type:'normal'},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                    {
                        prop:'', name:'',
                        children:[
                            {prop:'',name:''},
                            {prop:'',name:''},

                        ]
                    },
                ],
                tabDataIII:[],
                //
                batchTitle: '',
                batchVisible: false,
                batchType: '',
                batchForm: {},
                //
                contractList: [],
                contractVisible: false,
                priceContract: ''
            }
        },
        watch: {
            'otherVariable.isChildEdit':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            }
        },
        mounted() {
            this.getData();
            this.getUnitList();
            // /
            this.listenResizeIII();
            this.changeSizeIII();
            this.tempWidth = this.otherVariable.contentWidth;
            //
            this.$refs.pricedetail.$el.oncontextmenu = function() {return false};
        },
        methods: {
            //
            getData(isFirst = true) {
                this.tableData = [];
                this.fetch(`/oms/sls/LoaderDoc/getLoaderAndContractInfo?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                        }
                    });
            },
            selectChange(val) {
                if(val === 0) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 101;
                }else if(val === 1) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 50;
                }else if(val === 2) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minPriceStatus = 100;
                    this.searchForm.maxPriceStatus = 100;
                }
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            handleReset(){
                this.searchForm = {minPriceStatus: 0,maxPriceStatus: 50};
                this.handleRefresh();
            },
            //
            handleRefresh() {
                this.getData();
                this.otherVariable.isChildEdit = false;
                this.tabDataI = [];
                this.showPrice = false;
                this.showUnPrice = false;
                this.detailForm = {};
            },
            //
            handleDetail(row) {
                this.isShowNotAllowed = true;
                this.loadingI = true;
                if(row.priceStatus === 0 || row.priceStatus === null) {
                    this.showPrice = true;
                    this.showUnPrice = false;
                }else if(row.priceStatus === 50){
                    this.showPrice = false;
                    this.showUnPrice = true;
                }else {
                    this.showPrice = false;
                    this.showUnPrice = false;
                }
                this.isShowStatus.isRead = true;
                this.detailForm = row;
                this.showTable = false;
                this.refMatPrice = row.alPrice;
                this.tabDataIII = [];
                this.contractCode = row.contractCode;
                this.detailId = row.fid;
                this.priceContract = row.contractCode;
                this.docCode = row.docCode;
                this.getDetailTable(row.fid)
            },
            //
            getDetailTable(fid) {
                this.tabDataI = [];
                this.copyDataI = [];
                this.priceIdsArray = [];
                this.otherVariable.isChildEdit = false;
                this.isShowNotAllowed = true;
                this.getAjax(`/oms/sls/SalesLoaderPriceController/getLoaderPrice?loaderFlowId=${fid}&contractCode=${this.contractCode}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        this.isShowNotAllowed = false;
                        if(result.status === 200 && result.data) {
                            this.tabDataI = result.data;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                            result.data.forEach(item => {
                                item.alPrice = this.refMatPrice;
                                this.priceIdsArray.push(item.priceId)
                            });
                        }else {
                            this.tabDataI = [];
                            this.copyDataI = [];
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                    })
            },
            //
            handlePrice() {
                this.$confirm('?','',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(()=>{
                    let resultStatus;
                    this.fetch('/oms/sls/SalesLoaderPriceController/submitPrice', {loaderFlowId:this.detailId,priceStatus:50,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message:'',
                                    type:'success'
                                });
                                this.searchForm.tempStatus === 0 ? this.getData(false) : this.getData();
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error),
                                    type:'error'
                                })
                            }
                        })
                })

            },
            //
            handldUnPrice() {
                this.$confirm('?','',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(()=>{
                    let resultStatus;
                    this.fetch('/oms/sls/SalesLoaderPriceController/submitPrice', {loaderFlowId:this.detailId,priceStatus:0,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message:'',
                                    type:'success'
                                });
                                this.searchForm.tempStatus === 0 ? this.getData(false) : this.getData();
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error),
                                    type:'error'
                                })
                            }
                        })
                })

            },
            //
            clickDetail(row) {
                if(this.detailForm.isCheckPrice !== 1) {
                    this.showTable = false;
                    if(row.isLocked !== 1 && !this.otherVariable.isReadTable && this.showPrice) {
                        this.otherVariable.isChildEdit = true;
                        this.tabDataI = this.giveState(this.tabDataI);
                        row.isEditState = true;
                    }
                    this.childDetailRow = row || {};
                    this.clickTimes += 1;
                    if(this.clickTimes === 1) {
                        this.refMatProcessPrice = this.$lodash.cloneDeep(this.childDetailRow).pricePart1;
                    }
                    this.editIndex = this.$lodash.findIndex(this.tabDataI, ['priceId', row.priceId]);
                    if(this.editIndex < 0) {
                        this.editIndex = 0;
                    }
                }
            },
            //
            handleSaveChild() {
                let tempObj;
                let api = '/oms/sls/SalesLoaderPriceController/updatePcfMatPrice';
                let model = this.childDetailRow;
                model.sjgPrice = 0.000;
                model.jpPrice = 0.000;
                model.contractId = null;
                model.contractCode = null;
                model.alPriceDate = null;
                model.fid = this.childDetailRow.priceId;
                model.isLocked = this.childDetailRow.isLocked ? 1 : 0;
                model.price = Number(this.childDetailRow.price);
                model.refMatPrice = Number(this.childDetailRow.pricePart1);
                model.refMatProcessPrice = Number(this.childDetailRow.pricePart2);
                model.otherPrice = Number(this.childDetailRow.otherPrice);
                model.pricePart1 = Number(this.childDetailRow.pricePart1);
                model.pricePart2 = Number(this.childDetailRow.pricePart2);
                model.pricePart3 = Number(this.childDetailRow.pricePart3);
                model.pricePart4 = Number(this.childDetailRow.pricePart4);
                model.usrPricePart1 = Number(this.childDetailRow.usrPricePart1);
                model.usrPricePart2 = Number(this.childDetailRow.usrPricePart2);
                model.usrPricePart3 = Number(this.childDetailRow.usrPricePart3);
                model.usrPricePart4 = Number(this.childDetailRow.usrPricePart4);
                model.usrPricePart5 = Number(this.childDetailRow.usrPricePart5);
                model.usrPricePart6 = Number(this.childDetailRow.usrPricePart6);
                model.usrPricePart7 = Number(this.childDetailRow.usrPricePart7);
                model.usrPricePart8 = Number(this.childDetailRow.usrPricePart8);
                model.usrPricePart9 = Number(this.childDetailRow.usrPricePart9);
                model.usrPricePart10 = Number(this.childDetailRow.usrPricePart10);
                tempObj = {
                    loaderFlowId: this.detailForm.fid,
                    priceOperateModel: model
                };
                this.fetch(api,tempObj)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                            this.clickTimes = 0;
                        }else {
                            this.$message({
                                message: this.returnErrorMsg(result.error),
                                type: 'error'
                            });
                        }
                    });
                this.tabDataI = this.giveState(this.tabDataI)
            },
            //
            handleLock() {
                this.getAjax(`/oms/sls/SalesOrderPriceController/doLockPrice?priceId=${this.childDetailRow.priceId}`)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                        }else {
                            this.$message({
                                message: '',
                                type: 'error'
                            });
                        }
                    })
            },
            toggleChildMatcode() {
                this.isShowChildMatCode = !this.isShowChildMatCode;
                if(!this.isShowChildMatCode) {
                    this.tabDataI = this.$lodash.cloneDeep(this.copyDataI)
                }else {
                    this.tabDataI = this.tabDataI.filter(item => !item.parentVariantId)
                }
            },
            //
            handleCancelChild() {
                this.isShowChildMatCode = false;
                this.tabDataI = this.$lodash.cloneDeep(this.copyDataI);
                this.otherVariable.isChildEdit = false;
                this.clickTimes = 0;
            },
            //
            handleNotAllowed() {
                if(this.otherVariable.isChildEdit) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.handleCancelChild();
                    }).catch(() => {
                        console.log('cnacel')
                    })
                }
            },
            isSaveChildRow() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.handleSaveChild();
                }).catch(() => {
                    console.log('cancel')
                    //this.handleCancelChild();
                })
            },
            //
            showBatchDialog(column, event) {
                if(!this.otherVariable.isChildEdit) {
                    this.batchType = '';
                    const arr = ['(/m)','','','','','','','','','','',''];
                    if(arr.includes(column.label) && this.showPrice) {
                        this.batchType = column.label;
                        this.batchVisible = true;
                        this.batchForm = {};
                        if(this.batchType === '(/m)') {
                            this.batchTitle = ''
                        }else if(this.batchType === '' || this.batchType === '') {
                            this.batchTitle = `${this.batchType}`
                        }else {
                            this.batchTitle = `${this.batchType}`
                        }
                    }
                }
            },
            handleBatchEdit() {
                //this.detailId
            },
            //
            handleOtherTable(row) {
                if(this.otherVariable.isChildEdit) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }else {
                    this.showTable = !this.showTable;
                    if(this.showTable) {
                        this.isResize = false;
                        this.tableHeightI = this.tableHeightI - 150;
                        this.showTable = true;
                        this.$nextTick(() => {
                            this.isResize = true;
                        })
                    }else {
                        this.tableHeightI = this.tableHeightI + 150;
                    }
                    this.tabDataIII = [];
                    //
                    this.fetch('/oms/sls/SalesOrderPriceController/getMatPriceItem', {matPriceId:row.priceId})
                        .then(result => {
                            if (result.status === 200) {
                                this.tabDataIII = result.data.list;
                            }else {
                                this.tabDataIII = [];
                            }
                        })
                        .then(() => {
                            if(this.showTable) {
                                this.$refs['tabTableII'].initStatus();
                            }
                        })
                }
            },
            //
            getUnitList() {
                this.getAjax('/basedata/sys/DataDictionary/getDataDictionary?docCode=30126')
                    .then(result => {
                        if(result.data) {
                            result.data.forEach(item => {
                                item.dictValue = Number(item.dictValue)
                            });
                            this.unitList = this.formatArray(result.data, 'dictText', 'dictValue');
                        }
                    })
            },
            // 
            getContractList(val) {
                this.contractList = [];
                if(val) {
                    this.fetch('/oms/pqm/Contract/getContractMaster?pageSize=1000',{docCode:val})
                        .then(result => {
                            if(result.data && result.data.list) {
                                this.contractList = result.data.list;
                            }else {
                                this.contractList = [];
                            }
                        })
                }else {
                    this.contractList = [];
                }
            },
            // Edit By RayLua
            // 
            handleSaveContract() {
                if(this.detailId && this.priceContract) {
                    let resultStatus;
                    this.fetch(`/oms/sls/LoaderDoc/append-contract`, {fId:this.detailId, contractCode: this.priceContract})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.contractVisible = false;
                                this.getData(false);
                            }else {
                                let msg = (result && result.message) || (result && result.data && result.data.message);
                                this.$message({
                                    message: msg || '',
                                    type: 'error'
                                });
                            }
                        })
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
    .menu-icon-finance {
        background-size: 27px !important;
        background-position: center;
    }
    .btn-position-I {
        position: absolute;
        bottom: 2px;
        right: 40px!important;
    }
    .btn-position-II {
        position: absolute;
        bottom: 2px;
        right: 100px!important;
    }
    .btn-position-III {
        position: absolute;
        bottom: 2px;
        right: 160px!important;
    }
</style>



